.cat-info {
  display: flex;
  align-items: flex-start;
  margin-top: 20px;
}
.loader_circle {
  position: absolute;
  left: 50%;
  top: 25%;
  width: 50px;
  height: 50px;
  border: 5px solid white;
  border-bottom-color: red;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 0.5s linear infinite;
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
