.loader_circle {
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: red;
  border-radius: 50%;
  animation: rotation .5s linear infinite;
  display: inline-block;
  position: absolute;
  top: 25%;
  left: 50%;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

